import {
    createElement,
    createRef,
    DetailedHTMLProps,
    Fragment,
    HTMLAttributes,
    useEffect,
    useRef,
    useState
} from "react";

type TypedProps = {
    type: 'span' | 'div'
    text: string;
    speed: number;
} & Omit<DetailedHTMLProps<HTMLAttributes<HTMLElement>, HTMLElement>, 'options'>;

let i = 0;

const TypedWrapper = (props: TypedProps) => {

    const {children} = props;

    const ref = createRef<HTMLSpanElement>();

   //const theaterJS = require('theaterjs');

    const [obj, setObj] = useState(null);

    useEffect(() => {

        if (ref.current) {
            const fn = () => {
                if (i < props.text.length) {

                    if(ref.current){

                        if(props.text.charAt(i) == '\\' && props.text.charAt(i+1) == 'n'){
                            ref.current.innerHTML += "<br/>";
                        }else{
                            ref.current.innerHTML += props.text.charAt(i);
                        }

                        i++;

                        setTimeout(fn, props.speed + (200 * Math.random()));
                    }

                }
            };

            fn();
        }

        return () => {
            if(ref.current){

                ref.current.innerHTML = '';
            }
        }
    }, []);

    return createElement(props.type, {...props, ref, style: {...props.style, whiteSpace: 'pre-line'}});
};

export default TypedWrapper;